import React, { useEffect, useState } from 'react';
import { Row, Col } from 'react-bootstrap';
import Img from 'gatsby-image';

const PostDetail = ({ node }) => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const { title, images, description, date, videos, url } = node;

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <div className="post-modal-content">
      <Row>
        <Col>
          <h2 className="post-title">{title}</h2>
        </Col>
        <Col>
          <h3 className="post-date">{date}</h3>
        </Col>
      </Row>
      <p className="description">
        {description.description}
        <br></br>
        {url && (
          <a
            target="_blank"
            rel="noopener noreferrer"
            className="cta-btn color-text link-text"
            href={url}
          >
            [Link]
          </a>
        )}
      </p>

      {images.map((image) => {
        return <Img className="post-image" fluid={image.fluid} />;
      })}
      {videos != null &&
        videos.map((video) => {
          return <video className="post-video" src={video.file.url} controls></video>;
        })}
    </div>
  );
};

export default PostDetail;
